
/**
 * 格式化时间戳
 *
 * @param {Number} timestamp 需要格式化的时间戳
 * @param {String} timeformat YYYY 或 YY, MM 或 M, DD 或 D, hh 或 h(可选), mm 或 m(可选), ss 或 s(可选)组成的时间格式，
 *                     例如：'YYYY年MM月DD日 hh:mm:ss'
 * @author 文桥 <wenqiao1@xiaomi.com> 2018/03/15
 */
/* eslint-disable func-names */
export function format(timestamp, timeformat = 'YYYY年MM月DD日 hh:mm:ss') {
    const date = new Date(timestamp);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const hour = date.getHours();
    const minute = date.getMinutes();
    const second = date.getSeconds();
    const f = function (string) {
        return (`0${string}`).slice(-2);
    };
    const map = {
        YYYY: year,
        YY: f(year),
        MM: f(month),
        M: month,
        DD: f(day),
        D: day,
        hh: f(hour),
        h: hour,
        mm: f(minute),
        m: minute,
        ss: f(second),
        s: second,
    };

    return timeformat.replace(/Y{2,4}|M{1,2}|D{1,2}|h{1,2}|m{1,2}|s{1,2}/g, data => map[data] || data);
}

/**
 * 计算与当前时间的间隔
 * 参考：https://zhuanlan.zhihu.com/p/32905006
 *
 * @param {Number} timestamp 需要计算的时间戳
 * @param {String} limit 时间间隔的上限，
 *                 枚举类型的可选参数（day、week、month、year），默认 week
 * @param {String} timeformat 时间间隔的上限，后的时间格式化格式；与 BTime__format 的 format 参数一致
 * @return {String} 时间间隔
 * @author 文桥 <wenqiao1@xiaomi.com> 2018/03/15
 */
export function ago(timestamp, limit = 'day', timeformat = 'YYYY年MM月DD日 hh:mm:ss') {
    const array = ['minute', 'hour', 'day', 'week', 'month', 'year'];
    const milliseconds = [
        1000, // 1 * 1000
        60000, // 60 * 1000,
        3600000, // 60 * 60 * 1000,
        86400000, // 24 * 60 * 60 * 1000,
        604800000, // 7 * 24 * 60 * 60 * 1000,
        2592000000, // 30 * 24 * 60 * 60 * 1000,
        31104000000, // 12 * 30 * 24 * 60 * 60 * 1000,
    ];
    const text = ['秒前', '分钟前', '小时前', '天前', '周前', '月前', '年前'];
    // const lenMap = {
    //     day: 3,
    //     week: 4,
    //     month: 5,
    //     year: 6,
    // };
    // const limitLen = lenMap[limit];
    const durationMilliseconds = Date.now() - timestamp;

    // 职责链模式
    for (let i = 0, len = milliseconds.length; i < len; i += 1) {
        if (i + 1 === len || durationMilliseconds <= milliseconds[i + 1]) {
            const duration = Math.floor(durationMilliseconds / milliseconds[i]);

            if (duration === 0) {
                return '刚刚';
            }
            return duration + text[i];
        }

        if (limit === array[i]) {
            return format(timestamp, timeformat);
        }
    }

    return format(timestamp, timeformat);
}

/**
 * 格式化秒
 *
 * @param {Number} second 需要格式化的秒
 * @param {String} sencondformat hh 或 h(可选), mm 或 m(可选), ss 或 s(可选)组成的时间格式，
 *                     例如：'hh:mm:ss'
 * @author 文桥 <wenqiao1@xiaomi.com> 2018/03/15
 */
export function secondFormat(second, sencondformat = 'hh:mm:ss') {
    const nextSecond = parseFloat(second) || 0;
    const seconds = [
        60, // 60,
        3600, // 60 * 60,
    ];
    const map = {
        60(s) {
            return [0, 0, s];
        },
        3600(s) {
            const min = Math.floor(s / 60);
            return [0, min, (s - (min * 60))];
        },
    };
    const f = function (number) {
        return (`0${number}`).slice(-2);
    };
    const formatMap = {
        1(params) {
            return params;
        },
        2(params) {
            return f(params);
        },
    };
    const formatExec = function (target, sfm, fm) {
        return sfm.replace(/(h+)(.?)(m+)(.?)(s+)/gi, (...args) => {
            const data = Array.prototype.slice.call(args, 1, 6);
            let i = 0;

            return data.map((item, index) => {
                let time;

                if (index % 2 === 0) {
                    time = fm[item.length](target[i]);
                    i += 1;

                    return time;
                }

                return item;
            }).join('').replace(/^(00:)/g, '');
        });
    };
    const array = [];

    for (let i = 0, len = seconds.length; i < len; i += 1) {
        if (nextSecond < seconds[i]) {
            return formatExec(map[seconds[i]](nextSecond), sencondformat, formatMap);
        }
    }

    array[0] = Math.floor(nextSecond / 3600);
    array[1] = Math.floor((nextSecond - (array[0] * 3600)) / 60);
    array[2] = (nextSecond - (array[0] * 3600)) - (array[1] * 60);

    return formatExec(array, sencondformat, formatMap);
}

export function isSameDay(d1, d2) {
    return d1.getFullYear() === d2.getFullYear() &&
      d1.getMonth() === d2.getMonth() &&
      d1.getDate() === d2.getDate();
}
