<template>
    <div class="statistics">
        <div :class="['title',statisticsInfo.length > 0 ? '':'warn' ]">
            {{ title }}
        </div>
        <div
            class="item"
            @click="goUserTab(item)"
            v-for='(item,index) in statisticsInfo'
            :key='index'>
            <div class="order">
                <span>{{ index+1 }}</span>
                <img
                    class="head-icon"
                    :src='item.icon || userIcon'>
            </div>
            <div class="user-name">
                {{ item.realName }}
            </div>
            <div :class="['timer',index < 3 ? 'top-timer' : '']">
                {{ getTime(item.duration) }}
            </div>
        </div>
    </div>
</template>

<script>
import getStatisticsInfo from '@/api/statistics';
import { goUserProfile } from '@/utils/jsApiV2/deeplink';
import { secondFormat } from '@/utils/time';
import config from '@/config';

export default {
    name: 'Statistics',
    data() {
        return {
            title: '想看使用时长排行',
            statisticsInfo: [],
            userIcon: config.default.userIcon,
        };
    },
    created() {
        this.$root.hidePlaceholder();
        this.getInfo();
    },
    methods: {
        goUserTab(item) {
            goUserProfile({
                usnickname: item.nickName,
                userId: item.userId,
            });
        },
        getInfo() {
            getStatisticsInfo()
                .then((res) => {
                    this.statisticsInfo = res.details.sort((a, b) => b.duration - a.duration);
                })
                .catch(() => {
                    this.title = '暂无权限查看';
                });
        },
        getTime(time) {
            if (time <= 0) {
                return '0秒钟';
            }
            const arr = secondFormat(time).split(':');
            let str = '';
            const obj = {
                小时: '',
                分钟: '',
                秒钟: '',
            };
            const keysArr = Object.keys(obj);
            let i = keysArr.length - 1;
            let j = arr.length - 1;
            while (i >= 0 && j >= 0) {
                if (parseInt(arr[j], 10)) {
                    str = `${arr[j] - 0}${keysArr[i]}${str}`;
                }

                i -= 1;
                j -= 1;
            }
            return str;
        },
    },
};
</script>

