        height: 50px;
        border-bottom: 1px solid #ededed;
        font: 16px/50px miui;
        padding: 0 15px;
    }

    .order {
        display: inline-block;
        overflow: hidden;
    }

    .order span {
        display: inline-block;
        width: 20px;
        text-align: center;
    }

    .head-icon {
        display: inline-block;
        width: 34px;
        height: 34px;
        border-radius: 50%;
        margin-left: 10px;
        vertical-align: middle;
    }

    .user-name {
        display: inline-block;
        width: 100px;
        margin-left: 10px;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .timer {
        float: right;
        color: #08c05f;
    }

    .top-timer {
        color: #f99419;
    }
</style>
